#Magic .popup-box h2 {
    margin-bottom: 20px;
}

#Magic .form {
    display: flex;
    flex-wrap: wrap;
    overflow: visible;
    justify-content: center;
    align-items: center;
    height: 155px;
    width: 100%;
}

#Magic .form div {
    height: 5px;
    font-size: 25px;
}

#Magic .form div label {
    font-size: 20px;
    width: 150px;
}

#Magic .form div input {   
    font-size: 20px;
    width: 55px;
    margin-right: 15px;
    border: none;
}
#Magic .input-form {
    font-size: 25px;
}

#Magic .gun-inp {
    font-size: 18px;
    margin: 16px;
    width: 180px;
}

#Magic .form textarea {
    height: 100px;
    border: 1px #fff solid;
    background: none;
    margin-right: 10px;
    font-size: 17px;
    padding: 10px;
    outline: none;
    border-radius: 3px;
    flex: 1 1 10px;
}






