#Edit {
    position: fixed;
}

.DoABarrelRoll {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    
    align-content: flex-start;
    justify-content: center;
    font-size: 18px;
    display: flex;
    flex-wrap: wrap;
    height: 85%;
    width: 100%;
}

.DoABarrelRoll .dice-value {
    margin-top: 40px;
    animation: appear 2s;
    font-size: 80px;
    background: none;
}

.DoABarrelRoll img {
    animation: roll 2s both;
    height: 120px;
    width: 120px;
    background: none;
}

.DoABarrelRoll .per-value {
    display: flex;
    justify-content: center;
    font-size: 50px;
    position: absolute;
    animation: slow-appear 2s;
    margin-top: 130px;
    margin-left: 30px;
}

@keyframes roll {
    0% {
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(720deg);
    }
}

@keyframes appear {
    0% {
        color: rgba(0, 0, 0, 0);
    }
    100% {
        color: rgba(255, 255, 255, 1);
    }
}

@keyframes slow-appear {
    0% {
        color: rgba(0, 0, 0, 0);
    }
    50% {
        color: rgba(0, 0, 0, 0);
    }
    100% {
        color: rgba(255, 255, 255, 1);
    }
}