#Edit {
    position: fixed;
}

.form {
    font-size: 18px;
    display: flex;
    flex-wrap: wrap;
    height: 85%;
    overflow: scroll;
}

.input-form {
    margin-top: -10px;
    flex: 1 1 10px;
    border: none;
    height: 40px;
    font-size: 18px;
}

.form p {
    text-transform: uppercase;
    flex: 1 1 10px;
}

.form div {
    flex: 1 1 100%;
    margin-left: 12px;
    margin-top: 12px;
    display: flex;
    flex-wrap: wrap;
    height: 20px;
}

.save {
    background: none;
    cursor: pointer;
    background: none;
    height: 30px;
    width: 30px;
    font-size: 30px;
    border: none;
    transform: translateX(5px);
}

.save img {
    background: none;
    margin-top: 2px;
    height: 25px;
    width: 25px;
    transform: translateY(3px);
}
