#edit-popup {
    position: fixed;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    background: rgba(0, 0, 0, 0.5);
    padding:0;
    margin:0;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
}

.popup-box {
    color: #000;
    background: #000;
    border: 1px solid #fff;
    border-radius: 3px;
}

.close-popup {
    cursor: pointer;
    background: none;
    font-family: sans-serif;
    height: 25px;
    width: 25px;
    font-size: 25px;
    border: none;
}