.save-choice {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    text-decoration: none;
    font-size: 20px;
    align-items: center;
    height: 30px;
    margin-bottom: 32px;
    width: 140px;
    margin-left: 30px;
    border: 1px solid #fff;
    border-radius: 2px;
}

.save-choice:hover {
    cursor: pointer;
    border: 1px solid #999;
}

.save-choice a {
    height: 70px;
    height: 70px;
    outline: none;
}

.save-choice a:hover{
    cursor: pointer;
}

.save-choice input:hover {
    cursor: pointer;
}

.save-choice input {
    height: 100vh;
    width: 100vw;
}

.save-choice p {
    font-size: 26px;
    background: none;
    margin-top: 5px;
}

.save-choice label {
    font-size: 26px;
    background: none;
    margin-top: 5px;
}

.save-choice .clicker {
    font-size: 26px;
    background: none;
    margin-top: 5px;
}

.save-choice img {
    height: 60px;
    width: 60px;
}