.ret {
    background-color: white;
    height: 25px;
    width: 190px;
    display: flex;
    flex-wrap: wrap;
    border-radius: 2px;
}

.ret p {
    color: #000;
    font-size: 25px;
    margin-bottom: 12px;
    margin-top: 2px;
    background: none;
    margin-left: 50px;
}

.ret input {
    font-size: 25px;
}

.detail button {
    height: 25px;
    font-size: 25px;
    margin-right: 10px;
}

.detail button:hover {
    cursor: pointer;
}