.InvList {
    list-style-type: none;
    width: 100%;
    height: 100%;
}

.InvList li {
    border-bottom: #888 dashed 1px;
    align-items: center;
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    font-size: 25px;
    height: 50px;
    width: 100%;
    margin-top: 5px;
}

.InvList li p {
    flex: 1 1 0;
    margin-left: 15px;
}

.InvList li label {
    margin-right: 55px;
}

.InvList li button {
    cursor: pointer;
    border: none;
    font-size: 25px;
    margin-right: 15px;
    height: 25px; width: 25px;
}