#Guns .popup-box h2 {
    margin-bottom: 20px;
}

#Guns .form {
    display: flex;
    flex-wrap: wrap;
    overflow: visible;
    justify-content: center;
    align-items: center;
    height: 155px;
    width: 100%;
}

#Guns .InvList {
    overflow: scroll;
}

#Guns .InvList li {
    height: 100px;
}

#Guns .InvList li .gun-ndrow {
    display: flex;
    padding-left: 100%;
    padding-right: 100%;
    margin-top: -10px;
    background: none;
}

#Guns .InvList li label {
    width: 80px;
    height: 20px;
    background: none;
}

#Guns .form div {
    height: 5px;
    font-size: 25px;
}

#Guns .form div p {
    width: 45px;
    font-size: 25px;
    margin-bottom: 15px;
}

#Guns .form div .dice-inputs {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

#Guns .form div .dice-inputs input {
    border: none;
    width: 45px;
    height: 25px;
    font-size: 25px;
}

#Guns .form div .dice-inputs label {
    margin-right: 8px;
}

#Guns input {
    font-size: 20px;
    width: 55px;
    margin-right: 15px;
    border: none;
}

#Guns .input-form {
    font-size: 25px;
}
#Guns li .gun-title {
    font-size: 25px;
    text-align: center;
    margin-right: 50px;
}

#Guns .gun-inp {
    font-size: 18px;
    margin: 16px;
    padding-right: 25px;
}

#Guns .bttsstuff {
    display: flex;
    padding-top: 15px;
    margin: 0;
    width: 85px;
    height: 45px;
    flex: 0;
    background: none;
}

#Guns .bttsstuff img {
    background: none;
}

#Guns .ammo-inp {
    overflow: hidden;
    font-size: 18px;
    height: 58px;
    width: 90px;
    padding-bottom: 20px;
    background: none;
}

#Guns .ammo-inp img {
    margin-left: 6px;
    margin-right: 2px;
    margin-top: 12px;
    height: 20px; width: 20px;
}

#Guns .ammo-inp label {
    margin-left: 1px;
    overflow: hidden;
    font-size: 20px;
    height: 18px;
}

#Guns .InvList .byebye {
    margin-left: 480px;
    margin-top: -200px;
}

.InvList .editGun {
    width: 50px;
    margin-left: 15px;
}

.InvList .editGun button {
    font-size: 18px;
    text-decoration: underline;
}