@import url('https://fonts.googleapis.com/css2?family=Special+Elite&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Display&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cinzel:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=League+Script&display=swap');

* {
  margin: 0;
  padding: 0;
  font-family: 'Special Elite';
  font-weight: 100;
  background: black;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  color: #fff;
  scrollbar-width: none;
}

img {
  background: none;
}

::-webkit-scrollbar {
  display: none;
}


input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

input[type=number] {
  -moz-appearance: textfield;
}

h2 {
  margin-top: 20px;
  font-size: 30px;
  font-weight: 100;
  text-align: center;
  text-transform: uppercase;
  background-color: none;
}

input:focus {
  border: 0;
  outline: none;
}

textarea:focus {
  border: 0;
  outline: none;
}

select:focus {
  border: 0;
  outline: none;
}

textarea {
  resize: none;
}

.d20 {
  text-align: center;
  margin-top: -10px;
  height: 30px;
  width: 30px;
  padding-left: 10px;
}

.d20 img {
  height: 30px;
  width: 30px;

  transition: transform 1s;
}

.d20 img:hover {
  transform: rotate(360deg); 
  cursor: pointer;
}

#root {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

#info {
  margin: 5px;
  width: 500px;
  height: 300px;
  border: 1px solid #fff;
  border-radius: 3px;
  overflow: scroll;
}

#info h2 {
  padding-bottom: 20px;
}

#info button {
  margin-left: 10px;
  margin-top: -60px;
}

#info .detail {
  border: none;
}

.detail {
  margin: 15px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  background-color: none;
  border-bottom: rgba(255, 255, 255, 0.4) solid 1px;
}

.detail p {
  flex: 1 1 15px;
  font-size: 23px;
}

#info .detail label {
  width: 150px;
  overflow: hidden;
}

.detail label {
  font-size: 22px;
  margin-bottom: 5px;
}

.detail textarea {
  height: 25px;
  width: 55%;
  font-size: 25px;
  border: 0;
}

.detail select {
  font-size: 25px;
  width: 55%;
  border: 0;
  outline: none;
}

.detail option {
  border: 0;
  outline: none;
}

#profile {
  margin: 5px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  height: 300px;
  width: 500px;
  overflow: visible;
  justify-content: center;
  background: none;
}

.pfp {
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: center;
  height: 160px;
  width: 160px;
  padding: 20px;
  margin-top: 30px;
  margin-left: -80px;
}

.profile-frame {
  border: 3px solid #fff;
  border-radius: 50%;
  height: 150px; width: 150px;
  box-shadow: #888 0px 0px 2px 2px ;
}

.profile-frame img {
  height: 150px;
  width: 150px;
  border-radius: 50%;
}

.nametags {
  margin-top: 100px;
  height: 30px;
}

.caract_name p {
  background: none;
  font-size: 40px;
}

.player_name p {
  height: 30px;
  background: none;
  font-size: 25px;
}

.checkbox_container {
  height: 80px;
  width: 120px;
  margin-right: 320px;
  display: flex;

  flex-wrap: wrap;
  flex-direction: column;
}

.checkbox {
  margin-top: 7px;
  margin: 5px;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  height: 20px;
  width: 130px;
}

.checkbox p {
  padding-left: 3px;
  padding-right: 3px;
  font-size: 15px;
}

.checkbox input {
  height: 15px;
  width: 15px;
}

.MA {
  border: 2px solid #fff;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 28px;
  width: 90px;
  height: 42px;
}

.MA textarea {
  background: none;
  font-size: 28px;
}

.MA p {
  background: none;
}

#Points {
  margin: 5px;
  border: 1px solid #fff;
  height: 270px;
  width: 500px;
  border-radius: 3px;
}

#Points #Score {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-content: center;
  height: 170px;
  width: 500px;
  padding-top: 40px;
  margin-left: 15px;
  background: none;
}

#Points .detail {
  padding-left: 15px;
  margin-top: 30px;
  width: 180px;
  height: 50px;
  border: none;
}

#Points input {
  margin-top: -2px;
  font-size: 25px;
  width: 35px;
  height: 35px;
  border: 0;
  outline: none;
}

#Caract {
  margin: 5px;
  border: 1px solid #fff;
  height: 270px;
  width: 500px;
  border-radius: 3px;
}

#Caract .roll-title {
  width: 20px;
  font-size: 20px;
}

#Caract textarea {
  height: 20px;
  width: 40px;
  font-size: 20px;
}

#Caract .detail {
  border: none;
}

#Caract .detail p {
  flex: 0 75px;
}

#Caract .Skelect {
  padding-left: 13px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: 5px;
  height: 270px;
  width: 500px;
  background: none;
}

.roll {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.roll button {
  border: 0;
}

#Caract .roll {
  height: 20px;
  width: 180px;
}

#Attributes {
  margin: 5px;
  height: 500px;
  width: 500px;
  border: 1px solid #fff;
  border-radius: 3px;
  overflow: scroll;
}

#Attributes .roll-title {
  text-transform: uppercase;
  font-size: 20px;
  width: 50%;
}

#Attributes textarea {
  width: 200px;
  font-size: 20px;
}

#Guns {
  margin: 5px;
  border: 1px solid #fff;
  border-radius: 3px;
  width: 500px;
  height: 500px;
}

#Inventory {
  margin: 5px;
  height: 500px;
  width: 500px;
  border: 1px solid #fff;
  border-radius: 3px;
}

#Guns .popup-btn {
  font-size: 50px;
  cursor: pointer;
  border: 0;
  height: 20px;
  width: 100%;
}

#Inventory .popup-btn {
  font-size: 50px;
  cursor: pointer;
  border: 0;
  height: 20px;
  width: 100%;
}

.list {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  align-items: flex-end;
  border: 1px dashed #777;
  height: 80%;
  width: 99.5%;
  overflow: scroll;
}

#Story {
  margin: 5px;
  border: 1px solid #fff;
  border-radius: 3px;
  height: 500px;
  width: 500px;
  overflow: scroll;
}

.writeImg {
  background: none;
  height: 35px;
  width: 35px;
  border: 0;
  border-radius: 10%;
  transition: transform 0.5s;
}

.writeImg:hover {
  cursor: pointer;
  transform: translateY(-2px); 
}

.writeImg img {
  height: 25px;
  width: 25px;
}

#Edit {
  position: fixed;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  margin-right: 100%;
  margin-left: -160px;
  margin-top: 15px;
  position: absolute;
  height: 180px;
  width: 180px;
  border: 1px solid #fff;
  transition: margin-left 0.2s;
}

#Edit:hover {
  margin-left: 130px;
}

#Edit .popup-btn {
  margin-top: 10px;
  border: none;
  margin-left: 12px;
  font-size: 20px;
  height: 25px;
  width: 190px;
  transition: transform 1s;
}

#Edit .popup-btn:hover {
  cursor: pointer;
  transform: translateY(-5px);
}

#Edit .d20 {
  height: 60px;
  width: 60px;
  margin-right: 65px;
  margin-top: 5px;
  border: none;
}

#Edit .d20 img {
  height: 60px;
  width: 60px;
}

input[type='file']{
  display: none;
}

#upload label {
  color: #fff;
  cursor: pointer;
  margin-right: 41%;
  transition: transform 0.5s;
}

#upload label:hover {
  transform: translateY(-2px);
}

#noteblock {
  margin: 5px;
  border: 1px solid #fff;
  border-radius: 3px;
  height: 500px;
  width: 500px;
}

#Combat {
  margin: 5px;
  height: 500px;
  width: 500px;
  border: 1px solid #fff;
  border-radius: 3px;
}

#Combat #Attack {
  padding-bottom: 30px;
  width: 95%;
  height: 80px;
  margin: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

#Combat #Attack p {
  width: 100%;
  margin-left: 26%;
  font-size: 30px;
}

#Combat #Attack .aBuckett {
  margin-right: 10%;
}

#Combat #Attack .d20 {
  margin: 10px;
  margin-top: 5px;
}

#Combat #Attack img {
  height: 40px; width: 40px;
}

#Magic {
  margin: 5px;
  border: 1px solid #fff;
  border-radius: 3px;
  width: 500px;
  height: 500px;
}

#Magic .popup-btn {
  font-size: 50px;
  cursor: pointer;
  border: 0;
  height: 20px;
  width: 100%;
}