#Magic .list {
    overflow: scroll;
}

#Magic .list ul li{
    display: flex;
    flex-wrap: wrap;
    height: 350px;
    border: 0;
    border-bottom: ridge 1px #888;
    margin-top: 0;
}

#Magic .list ul li .pWrapper {
    text-align: center;
    margin-right: 50px;
    font-size: 25px;
    margin-top: 10px;
    width: 65%;
    height: 50px;
}

#Magic .list ul li .power-desc {
    height: 200px;
    width: 500px;
    word-wrap: break-word;
    overflow: scroll;
    margin-top: -15px;
    margin-left: 2%;
    margin-right: 2%;
    font-size: 20px;
    border: 1px solid #888;
    box-shadow: 0px 0px 2px 1px #888;
}

#Magic .list ul li .power-desc p {
    height: 130px;
    margin: 10px;
}

#Magic .list ul li .gun-inp {
    flex: 0;
    height: 20px;
    margin: 10px;
}

#Magic .byebye {
    margin-bottom: 15px;
}

#Magic .editPower button {
    margin-bottom: 15px;
    width: 55px;
    font-size: 20px;
    text-decoration: underline;
    margin-left: 10px;
}